<template>
  <transition>
    <header v-if="!raceDataIsFetching" class="race-header">
      <div class="race-header__top">
        <router-link class="race-header__logo-link" to="/">
          <svg xmlns="http://www.w3.org/2000/svg" class="race-header__logo" tabindex="-1" viewBox="0 0 139.005 48.124" width="139.005" height="48.124" v-bind:svg-inline="''" v-bind:class="'race-header__logo'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill="#fff" d="M69.893 2.902L56.43 29.565l-8.684-.075 9.964-19.33-4.02.061 4.074-7.359s11.414.04 12.68.04M74.589.086l-18.26.009-6.692 12.318h4.146l-9.538 19.336H58.42zm-40.15 2.098c3.256 0 7.41.095 12.791.131l-2.536 5.169h-14.85l-3.322 7.105H39.52l-2.601 5.17h-13.55l-4.435 9.69h-8.038C24.63 4.644 17.885 2.183 34.438 2.183m0-2.184c-6.12 0-9.713.342-12.399 2.191-2.536 1.755-3.658 4.5-5.511 9.042-1.638 4.026-3.893 9.545-8.216 17.356l-1.684 3.05h14.288c1.481-3.229 2.958-6.46 4.436-9.69h13.484c1.753-3.475 3.094-6.143 4.8-9.536h-13.1l1.282-2.738H46.64c1.706-3.467 3.009-6.127 4.67-9.508L47.275.138C42.885.126 38.612.003 34.438 0z" class="st1"/><path fill="none" stroke="#ed1c24" stroke-linecap="square" stroke-miterlimit="10" stroke-width="3" d="M129.39 12.95c-36.89-.74-36.4 3.64-33.49 4.26 29.05 6.27 26.13 18.21 5.48 22-2.8.51-37.2 8.36-99.09 7.32l4.18-9.41c58.26-.28 87.41-3.14 89.29-8.36 1.89-5.27-23.45-7.43-23.29-12.55.18-5.66 29.54-13.23 63.64-12.55z"/></svg>
        </router-link>
        <div class="race-header__info">
          <template v-if="$navigatorOnline">
            <button
              :class="['race-header__race-btn', { active: raceActive }]"
              v-on:click="activateRaceOverlay(true)"
              ref="heading"
            >
              <h1 class="race-header__race">{{ race.raceName }}</h1>
            </button>
            <span class="race-header__circuit">{{
              race.Circuit.circuitName
            }}</span>
          </template>
          <template v-else>
            <span
              class="race-header__race-btn race-header__race-btn--offline"
              ref="heading"
            >
              <h1 class="race-header__race">{{ race.raceName }}</h1>
            </span>
            <span class="race-header__circuit">{{
              race.Circuit.circuitName
            }}</span>
          </template>

          <span class="race-header__round-and-date">
            <span class="race-header__round"
              >Round {{ race.round }},&nbsp;</span
            >
            <button
              v-if="$navigatorOnline"
              :class="['race-header__year-btn', { active: yearActive }]"
              v-html="race.season"
              v-on:click="activateYearOverlay"
            ></button>
            <span
              v-else
              class="race-header__year-btn race-header__year-btn--offline"
              v-html="race.season"
            ></span>
            <span
              class="race-header__date"
              v-html="seperateDate(race.date)"
            ></span>
          </span>
        </div>
      </div>
      <RaceSelect
        v-if="!latestRaceDataFetching && $navigatorOnline"
        :latestRaceData="latestRaceData"
        :seasons="seasons"
        :race="race"
      ></RaceSelect>
    </header>
  </transition>
</template>

<script>
import RaceSelect from '@/components/RaceSelect'
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'race-header',
  components: {
    RaceSelect
  },
  props: {
    race: [Object, Boolean],
    raceDataIsFetching: Boolean,
    seasons: Array,
    latestRaceData: [Object, Boolean],
    latestRaceDataFetching: [Boolean]
  },
  data () {
    return {
      headingActive: true,
      yearActive: false,
      raceActive: false
    }
  },
  mounted () {
    // Emitted from raceSelect.vue
    EventBus.$on('activateRaceOverlay', () => {
      this.activateRaceOverlay()
    })
  },
  methods: {
    seperateDate (e) {
      // if (this.raceDataIsFetching) {
      return e.split(/[0-9]{4}/).pop()
      // }
    },
    activateYearOverlay () {
      this.raceActive = false
      !this.yearActive ? this.yearActive = true : this.yearActive = false

      // Emitted to RaceSelect.vue
      EventBus.$emit('raceSelectYearState')
    },
    activateRaceOverlay (bool) {
      this.yearActive = false
      !this.raceActive ? this.raceActive = true : this.raceActive = false

      if (bool) {
        // Emitted to RaceSelect.vue
        EventBus.$emit('raceSelectRaceState', true)
      } else {
        EventBus.$emit('raceSelectRaceState', false)
      }
    }
  }
}
</script>
